import { Block, Inline } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import { ELEMENT_TYPE } from '../../utils/meta';
import InlinePlaceholder from '../models';

export default {
  name: 'placeholder',

  match(node: JsonML): boolean {
    return getTagName(node) === ELEMENT_TYPE;
  },

  convert(node: JsonML): Block | Inline {
    return InlinePlaceholder.create({
      data: getAttributes(node) || {},
    });
  },
};
